<template>
  <div class="wrapper">
    <div class="swiper">
      <div class="swiper-container" ref="mySwiper">
        <div class="swiper-wrapper">
          <div class="swiper-slide">
            <img src="../assets/image/poster/photo1.png" alt="" />
          </div>
          <div class="swiper-slide">
            <img src="../assets/image/poster/photo2.png" alt="" />
          </div>
          <div class="swiper-slide">
            <img src="../assets/image/poster/photo3.png" alt="" />
          </div>
        </div>
        <!-- 如果需要导航按钮 -->
        <div class="swiper-button-prev prev"></div>
        <div class="swiper-button-next next"></div>
      </div>
    </div>
    <div class="btn" @click="goEnd()"></div>
    <div class="start"></div>
    <div class="light"></div>
  </div>
</template>

<script>
import Swiper from "../utils/swiper/swiper-bundle.min.js";
export default {
  name: "Poster",
  mounted() {
    let mySwiper = new Swiper(this.$refs.mySwiper, {
      loop: true, // 循环模式选项
      slidesPerView: 1,
      centeredSlides: true,
      slidesPerView: "auto",
      spaceBetween: 20,
      observer: true,
      observeParents: true,
      // 如果需要前进后退按钮
      navigation: {
        prevEl: '.swiper-button-prev',
        nextEl: '.swiper-button-next',
      },
    });
  },
  methods:{
    goEnd(){
      localStorage.setItem('isShow',true);
      localStorage.setItem('poster',document.querySelector('.swiper-slide-active').firstElementChild.src);
      this.$router.push('/end');
    }
  }
};
</script>

<style scoped>
/* 背景 */
.wrapper {
  width: 100%;
  height: 100%;
  background: url("../assets/image/poster/组\ 10.png") no-repeat 0 0;
  background-size: 100% 100%;
  animation: wrapper 2s ease-in;
}

@keyframes wrapper {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.swiper {
  width: 100%;
  height: 60%;
  position: absolute;
  top: 18%;
}
.swiper-container {
  width: 100%;
  height: 90%;
  margin-bottom: 10%;
}

.swiper-container .swiper-wrapper .swiper-slide {
  width: 80%;
}

.swiper-container .swiper-wrapper .swiper-slide img {
  width: 100%;
  height: 100%;
  box-shadow: -3px 4px 34px #999;
}

.swiper-container .swiper-wrapper .swiper-slide-prev {
  margin-top: 5%;
  height: 90% important;
}
.swiper-container .swiper-wrapper .swiper-slide-prev img {
  position: absolute;
  left: 15%;
  height: 90% important;
}

.swiper-container .swiper-wrapper .swiper-slide-next {
  margin-top: 5%;
  height: 90% important;
}
.swiper-container .swiper-wrapper .swiper-slide-next img {
  position: absolute;
  right: 15%;
  height: 90% important;
}

.swiper-container .swiper-wrapper .swiper-slide-active {
  width: 80%;
  z-index: 1;
}

.btn {
  width: 42%;
  height: 8%;
  background: url("../assets/image/poster/按钮.png") no-repeat 0 0;
  background-size: 100% 100%;
  animation: breathe 2s infinite;
  position: absolute;
  top: 78.31%;
  left: 28.97%;
}
@keyframes breathe {
  0% {
    width: 42%;
    height: 8%;
  }
  50% {
    width: 41%;
    height: 7.5%;
    left: calc(50% - 20.5%);
    top: 79.31%;
  }
  100% {
    width: 42%;
    height: 8%;
  }
}
.swiper .swiper-container .swiper-button-prev {
  width: 41px;
  height: 44px;
  background: url("../assets/image/poster/椭圆\ 2.png") no-repeat 0 0;
  background-size: 100% 100%;
  position: absolute;
}

.swiper .swiper-container .swiper-button-prev::after {
  content: "";
}

.swiper .swiper-container .swiper-button-next {
  width: 41px;
  height: 44px;
  background: url("../assets/image/poster/椭圆\ 2\ 拷贝.png") no-repeat 0 0;
  background-size: 100% 100%;
  position: absolute;
}

.swiper .swiper-container .swiper-button-next::after {
  content: "";
}

.start {
  width: 100%;
  height: 50%;
  background: url("../assets/image/poster/星星.png") no-repeat 0 0;
  background-size: 100% 100%;
  animation: showorconceal 3s 1s infinite;
  position: absolute;
  top: 0;
  left: 0;
}
@keyframes showorconceal {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.light {
  width: 100%;
  height: 38%;
  background: url("../assets/image/poster/顶光.png") no-repeat 0 0;
  background-size: 100% 100%;
  position: absolute;
  top: 0;
  left: 0;
}
</style>